import { PaginationService } from './pagination.service';
import { PaginationControlsDirective } from './pagination-controls.directive';
import {Component, Input, Output, EventEmitter, ChangeDetectionStrategy, ViewEncapsulation, ViewChild, ElementRef, Optional} from '@angular/core';

function coerceToBoolean(input: string | boolean): boolean {
    return !!input && input !== 'false';
}

// styleUrls: ['./pagination-controls.component.css'],

/**
 * The default pagination controls component. Actually just a default implementation of a custom template.
 */

//  #farris-gotopagenumber{ text-align: center;
//     -moz-appearance: textfield;
//     width: 40px;
// }
// #farris-gotopagenumber::-webkit-outer-spin-button,
// #farris-gotopagenumber::-webkit-inner-spin-button {
//     -webkit-appearance: none;
// }
@Component({
    selector: 'pagination-controls',
    templateUrl: './pagination-controls.component.html',
    styles: [],
    changeDetection: ChangeDetectionStrategy.OnPush,
    encapsulation: ViewEncapsulation.None
})
export class PaginationControlsComponent {

    private _directionLinks = true;
    private _autoHide = false;
    private _responsive = false;

    private mouseInSelectedList = false;

    /** æ¾ç¤ºæ¨¡å¼ï¼ default é»è®¤ï¼simple: ç®æ´ */
    @Input() viewMode = 'default';

    @Input() id: string;
    @Input() maxSize = 7;
    @Input()
    get directionLinks(): boolean {
        return this._directionLinks;
    }
    set directionLinks(value: boolean) {
        this._directionLinks = coerceToBoolean(value);
    }
    @Input()
    get autoHide(): boolean {
        return this._autoHide;
    }
    set autoHide(value: boolean) {
        this._autoHide = coerceToBoolean(value);
    }
    @Input()
    get responsive(): boolean {
        return this._responsive;
    }
    set responsive(value: boolean) {
        this._responsive = coerceToBoolean(value);
    }
    @Input() previousLabel = 'Previous';
    @Input() nextLabel = 'Next';
    @Input() screenReaderPaginationLabel = 'Pagination';
    @Input() screenReaderPageLabel = 'page';
    @Input() screenReaderCurrentLabel = `You're on page`;
    @Input() showPageList = true;
    @Input() showPageInfo = true;
    @Input() showPageNumber = true;

    @Input() showSelectedList = false;

    @Input() showGoToLast = false;
    @Input() showGoToFirst = false;

    /** æ¾ç¤ºè·³è½¬è³æå®é¡µç è¾å¥æ¡ */
    @Input() showGotoInput = false;

    @Input() position: 'left'|'center'|'right' = 'right';

    /**
     * @deprecated
     */
    @Input() message = 'æ¯é¡µ {0} æ¡è®°å½ï¼å± {1} æ¡è®°å½';
    /**
     * @deprecated
     */
    @Input() pageListFirstText = 'æ¾ç¤º';
    /**
     * @deprecated
     */
    @Input() pageListLastText = 'æ¡';

    @Output() pageChange: EventEmitter<number> = new EventEmitter<number>();
    @Output() pageSizeChange = new EventEmitter<number>();
    @ViewChild('p') paginationDirective: PaginationControlsDirective;

    @ViewChild('focusPageSize') focusPageSizeRef: ElementRef<any>;
    @ViewChild('pglistinfo') pglistinfo: ElementRef<any>;
    showPagesPanel = false;

    // constructor(@Optional() private pagerService: PaginationService) {
    //     if (!this.pagerService) {
    //         this.pagerService = new PaginationService();
    //     }
    // }

    onMouseLeave($event) {
        this.showPagesPanel = false;
        this.paginationDirective.changeDetectorRef.detectChanges();
    }

    onMouseEnter($event: MouseEvent) {
        if (this.paginationDirective.getTotalItems() === 0) {
            return;
        }
        this.showPagesPanel = true;
    }

    changePageSizeHandler($event, pagesize) {
        $event.stopPropagation();
        // å½åé¡µåæ¬¡ç¹å»
        if (pagesize === this.paginationDirective.getPageSize()) {
            return;
        }
        this.showPagesPanel = false;
        // this.removeOverlay();
        this.pageSizeChange.emit(pagesize);
    }

    onPageNumberChanged(inputEl: KeyboardEvent, maxNumber) {
        const target = inputEl.target as any;
        const currentPageNumber = Number(target.value);
        if (currentPageNumber > maxNumber) {
            target.value = maxNumber;
        } else {
            // if (currentPageNumber < 1) {
            //     target.value = 1;
            // }
        }
    }

    onPageNumberBlur(inputEl: any) {
        const target = inputEl.target as any;
        const currentPageNumber = Number(target.value);
        if (currentPageNumber < 1) {
            target.value = 1;
        }
    }

    setPageList(newpageList: number[]) {
        this.paginationDirective.setPageList(newpageList);
    }

    goto($event) {
        let newpager = Number($event.target.value);
        if (newpager < 1) {
            newpager = 1;
            $event.target.value = 1;
        }

        if (this.paginationDirective.getCurrent() !== newpager) {
            this.paginationDirective.goto(newpager);
        }
    }

}
