import { debounce } from 'rxjs/operators';
import { Component, OnInit, EventEmitter, Output, Input, OnChanges, SimpleChanges, HostBinding } from '@angular/core';
import { interval } from 'rxjs';

@Component({
    selector: 'pagination-selected-list',
    templateUrl: './selected-list.component.html',
})
export class PaginationSelectedListComponent implements OnInit, OnChanges {

    @Input() data = [];
    //ä½ç½®ï¼å³å®æ¯å¦ç¿»è½¬
    @Input() position="bottom";
    @Output() close = new EventEmitter();
    @Output() remove = new EventEmitter();
    @Output() clear = new EventEmitter();
    @HostBinding('class.pos-top') get slPosition() { return this.position=='top'; }

    constructor() { }

    ngOnInit(): void { }

    ngOnChanges(changes: SimpleChanges) {
    }

    onClose($event: MouseEvent) {
        $event.stopPropagation();
        this.close.emit();
    }

    onRemoveClick($event: MouseEvent, id: any) {
        $event.stopPropagation();
        this.remove.emit(id);
    }

    onClearClick($event: MouseEvent) {
        $event.stopPropagation();
        this.clear.emit();
    }

    onClick($event: MouseEvent) {
        $event.stopPropagation();
        return false;
    }
}
